<template>
    <div>
        <div data-v-4f011313="" class="page">
            <div data-v-152ab910="" data-v-4f011313="" class="sub-header">
                <div data-v-152ab910="" class="back" @click="$router.go(-1)"></div>
                <div data-v-152ab910="" class="title">下单成功</div><!----></div>
            <div data-v-4f011313="" class="main">
                <div data-v-4f011313="" class="list success">下单成功！</div>
                <div data-v-4f011313="" class="list ordernum">订单编号：{{orderNum}}</div>
                <div data-v-4f011313="" class="list" @click="$router.push('/user/order')">查看订单</div>
                <div data-v-4f011313="" class="pay-btn">去付款</div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapActions} from "vuex"
    export default {
        data(){
            return {
                orderNum:""
            }
        },
        created(){
            //安全认证
            this.$utils.safeUser(this)
            //获取订单编号
            this.getLastorderNum({success:(res)=>{
                    if (res.code ===200){
                        this.orderNum = res.data.ordernum
                    }
                }})
        },
        methods:{
            ...mapActions({
                //获取订单编号
                getLastorderNum:"order/getLastorderNum"
            })
        }
    }
</script>

